import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/components/Login'
import Index from '@/components/Index/Index'
import Overview from '@/components/overview/Overview' //
import Balancerecord from '@/components/Balancerecord/Balancerecord' //
import Unicomcard from '@/components/Cardmanagement/Unicomcard' //
import Trafficcard from '@/components/Cardmanagement/Trafficcard' //
import Telecommunicationcard from '@/components/Cardmanagement/Telecommunicationcard' //
import Detail from '@/components/Cardmanagement/Detail' //
import Singleprepaid from '@/components/Cardmanagement/Singleprepaid' //
import Batchtopup from '@/components/Cardmanagement/Batchtopup' //
import Batchthrough from '@/components/Cardmanagement/Batchthrough' //
import Agentlist from '@/components/Agentmanagement/Agentlist' //
import Addagent from '@/components/Agentmanagement/Addagent' //
import Sellingprice from '@/components/Agentmanagement/Sellingprice' //
import Agenttopup from '@/components/Agentmanagement/Agenttopup' //
import Operatingadiary from '@/components/Operatingadiary/Operatingadiary' //
import Echartspage from '@/components/Echartspage/Echartspage' //

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Login',
      component: Login
    },
	{
		path: '/Index',
		name: '/Index',
		component: Index,
		children: [
			{
				path: '/Overview',
				name: '/Overview',
				component: Overview,
			},
			{
				path: '/Balancerecord',
				name: '/Balancerecord',
				component: Balancerecord,
			},
			{
				path: '/Unicomcard',
				name: '/Unicomcard',
				component: Unicomcard,
			},
			{
				path: '/Trafficcard',
				name: '/Trafficcard',
				component: Trafficcard,
			},
			{
				path: '/Telecommunicationcard',
				name: '/Telecommunicationcard',
				component: Telecommunicationcard,
			},
			{
				path: '/Detail/:type',
				name: '/Detail',
				component: Detail,
			},
			{
				path: '/Singleprepaid',
				name: '/Singleprepaid',
				component: Singleprepaid,
			},
			{
				path: '/Batchtopup',
				name: '/Batchtopup',
				component: Batchtopup,
			},
			{
				path: '/Batchthrough',
				name: '/Batchthrough',
				component: Batchthrough,
			},
			{
				path: '/Agentlist',
				name: '/Agentlist',
				component: Agentlist,
			},
			{
				path: '/Addagent',
				name: '/Addagent',
				component: Addagent,
			},
			{
				path: '/Operatingadiary',
				name: '/Operatingadiary',
				component: Operatingadiary,
			},
			{
				path: '/Echartspage',
				name: '/Echartspage',
				component: Echartspage,
			},
			{
				path: '/Sellingprice/:id',
				name: '/Sellingprice',
				component: Sellingprice,
			},
			{
				path: '/Agenttopup/:id',
				name: '/Agenttopup',
				component: Agenttopup,
			},
			
			
		]
	},
		
  ]
})



