<style scoped="scoped" lang="less">
	.batchtopupbox{
		padding:20px;
		.topstepbox{
			p{
				display: flex;
				align-items: center;
				height: 60px;
				font-size: 18px;
				color: #333;
				font-weight: bold;
			}
			.ivu-steps-horizontal{
				height: 40px;
				margin-bottom: 20px;
				/deep/ .ivu-steps-main{
					display: inline-block;
				}
			}
		}
		.stepcontentbox{
			.stepone{
				display: flex;;
				flex-direction: column;
				color: #333;
				font-size: 14px;
				background: #F5F5f5;
				padding: 20px;
				box-sizing: border-box;
				border-radius: 10px;
				margin-bottom: 20px;
				h3{
					height: 30px;
				}
				.grouplist{
					display: flex;
					justify-content: space-between;
					
				}
				.groupbox{
					display: flex;
					flex-direction: column;
					margin-bottom: 20px;
					width: 44%;
					span{
						width: 100%;
						display: block;
						margin-bottom: 10px;
						color: #F16643;
						height: 42px;
						
						// overflow: hidden;
						// height: 30px;
						// line-height: 30px;
						// text-overflow: ellipsis;
						// white-space: nowrap;
					}
					input{
						min-width: 150px;
						max-width: 100%;
						height: 40px;
						display: flex;
						align-items: center;
						padding: 5px 10px;
						box-sizing: border-box;
						border-radius: 6px;
						border: 1px solid #ccc;
					}
				}
			}
		}
	}
</style>
	
<template>
	<div class="batchtopupbox">
		<div class="topstepbox">
			<p> {{ current +1}} </p>
			<Steps :current="current" status="wait">
				<Step title="/iccid"></Step>
				<Step title=""></Step>
			</Steps>
		</div>
		<!--  -->
		<div class="stepcontentbox">
			<div class="stepone" v-if="current==0">
				<h3> /iccid/iccid500</h3>
				<div class="grouplist">
					<div class="groupbox">
						<span>1000000001-1000000002 iccidiccd </span>
						<input type="text" placeholder="ICCID">
					</div>
					<div class="groupbox">
						<span>1000000001 </span>
						<input type="text" placeholder="ICCID">
					</div>
				</div>
			</div>
			<div class="stepone" v-if="current==1">
				<Form :model="formLeft" label-position="left" :label-width="100">
					<FormItem label="" >
						<Select v-model="model1" style="width:200px">
							<Option v-for="item in preSale" :value="item.value" :key="item.value">{{ item.label }}</Option>
						</Select>
					</FormItem> 
					<FormItem label="04">
						<Input v-model="formLeft.input5" disabled="disabled"></Input>
					</FormItem>
					<!--  -->
					<FormItem label="" >
						<Select v-model="model6" style="width:200px">
							<Option v-for="item in cityList" :value="item.value" :key="item.value">{{ item.label }}</Option>
						</Select>
					</FormItem> 
				</Form>
				<!-- -->
				<div class="btnbox">
					<Button type="primary" style="margin:30px 100px;"></Button>
				</div>
			</div>
			
			<Button  v-if="current!=0"  type="primary" @click="prefun"></Button>
			<Button  v-if="current!=1"  type="primary" @click="next"></Button>

		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				current:0,
				tem_params:"",
				formLeft: {
                    input1: '1440244215192',
                    input2: '8986061911001512717',
                    input3: '',
                    input4: '89720.50MB',
                    input6: '-89720.5MB',
                    input5: '04'
                },
				model1:0,
				preSale:[
                    {
                        value: '0',
                        label: ''
                    },
                    {
                        value: '1',
                        label: ''
                    }
				],
				model6:'',
				cityList: [
                    {
                        value: '',
                        label: ''
                    },
                    {
                        value: '0',
                        label: '1G/14.20'
                    },
                    {
                        value: '1',
                        label: '1G/34.80'
                    },
                    {
                        value: '2',
                        label: '2G/15.40'
                    },
                    {
                        value: '3',
                        label: '1G/65.40'
                    },
                    {
                        value: '4',
                        label: '1G/126.00'
                    }
                ],
			};
		},
		methods: {
			prefun(){
				if (this.current <=0) {
					this.current =0;
				} else {
					this.current -= 1;
				}
			},
			next() {
				if (this.current >= 1) {
					this.current = 0;
				} else {
					this.current += 1;
				}
			}
		},
        created(){
            console.log(this.$route.params)
            var tem_params = this.$route.params;
            this.tem_params = tem_params;
        }
	};
</script>



