import state from './state.js'   //vuex


const mutations = {
	// 
	logout: state => {
	  state.name=state.name+""
	},
	// 
	setuserinfo: (state,val) => {
	  state.card=val.card
		state.type=val.type
		state.user_name=val.user_name
		state.logo = val.logo
		state.token = val.token
		state.serial = val.serial
	},
}
// state
export default mutations



